@import url(https://fonts.googleapis.com/css2?family=Do+Hyeon&display=swap);
#subStage {
    position:fixed;
    width:100%;
    height:100%;
    pointer-events:none;
    z-index:2
}
html, body {
    margin: 0;
    height: 100%;
    font-family: 'Do Hyeon', sans-serif;
}
#stage {
    height: 100%;
}
.title-cover {
    background: rgba(0,0,0,0.6);
    height: 100%;
    width:100%;
}
.title-box {
    background: url(/archive/static/media/space-background.9e7311a5.jpg);
    width: 100%;
    height: 100%;
}
.title-info {
    position: absolute;
    margin-left: 40px;
    margin-top: 50px;
}
.title-lotinex {
    color: white;
    font-weight: bold;
    font-size: 100px;
}
.title-lotinex-info {
    color:  white;
    font-size: 30px;
}
.projectViewer {
    position: absolute;
    right:0;
    top:0;

}

.project-descriptor {
    position: absolute;
    background: rgba(0,0,0,0.7);
    border-radius: 7px;
    color: white;
    display: inline-block;
    padding: 5px;
}
.milestone-down {
    position: absolute;
    bottom: 0;
    width: 100%;
    color: white;
    height: 100px;
    font-size: 100px;
    text-align: center;
}
.bottom {
    width: 100%;
    height: 700px;
}
